<template>
	<div>
		<div class="loading_holder" v-if="loading">LOADING</div>
		<div class="password_holder" v-if="!loading && !password_applied">
			ENTER_PASSWORD
			<input type="password" v-model="password" @keyup.enter="checkPassword" />
		</div>
		<div
			class="logout"
			v-if="!loading && password_applied && database_is_encrypted"
		>
			<button class="logout" @click="logout">LOGOUT</button>
		</div>
		<div class="search_holder" v-if="!loading && password_applied">
			<input
				type="text"
				:placeholder="'SEARCH'"
				@keyup="search"
				v-model="search_query"
			/>
			<div class="results">
				<div class="no_results" v-if="results.length == 0">
					{{ "NO_RESULTS" }}
				</div>
				<div
					class="result"
					v-for="result of results"
					:key="'result' + result.id"
				>
					{{ result.a }}
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import axios from "axios";
	const crypto = require("crypto");
	const MiniSearch = require("minisearch");
	let miniSearch;
	let localstorage_password_key = `faqelize_${document.location.hostname}_password`;

	function decryptDatabase(value, password) {
		try {
			let algorithm = "aes256";
			let key = crypto
				.createHash("sha256")
				.update(password)
				.digest("base64")
				.substr(0, 32);
			let decipher = crypto.createDecipher(algorithm, key);
			let decrypted =
				decipher.update(value[1], "hex", "utf8") + decipher.final("utf8");
			return JSON.parse(decrypted);
		} catch (error) {
			return false;
		}
	}

	export default {
		data() {
			return {
				results: [],
				search_query: "",
				loading: true,
				password: "",
				password_applied: false,
				database_is_encrypted: false,
			};
		},
		methods: {
			async load() {
				let saved_password = localStorage.getItem(localstorage_password_key);
				if (saved_password) this.password = saved_password;
				this.loading = true;
				let database = await this.fetchDB();
				if (database.error) {
					console.error("Can't fetch database", database.error);
					if (database.error == "INVALID_PASSWORD") {
						this.loading = false;
						this.password_applied = false;
						localStorage.removeItem(localstorage_password_key);
					}
					return;
				}
				this.loading = false;
				this.password_applied = true;
				this.database_is_encrypted = database.encrypted;
				if (database.encrypted)
					localStorage.setItem(localstorage_password_key, this.password);
				if (!database.value[0].id) {
					for (let doc_i in database.value) database.value[doc_i].id = doc_i;
				}
				miniSearch = new MiniSearch.default({
					fields: ["q"],
					storeFields: ["q", "a"],
				});
				miniSearch.addAll(database.value);
			},
			search() {
				let results = miniSearch.search(this.search_query);
				this.results = results;
			},
			async fetchDB() {
				let value = [];
				let encrypted = false;
				let is_dev = process.env.NODE_ENV == "development";
				let content = await axios.get(
					!is_dev ? "database.json" : "database_encrypted.json" // TODO REMOVE !
				);
				value = content.data;
				if (value) {
					if (value[0] == "encrypted") {
						encrypted = true;
						value = decryptDatabase(value, this.password);
						if (!value) {
							return { error: "INVALID_PASSWORD", encrypted };
						}
					}
				}
				return { value, encrypted };
			},
			checkPassword(e) {
				if (e.keyCode == 13) {
					this.load();
				}
			},
			logout() {
				this.password_applied = false;
				localStorage.removeItem(localstorage_password_key);
				this.password = "";
			},
		},
		mounted() {
			this.load();
		},
	};
</script>

<style lang="scss" scoped>
	.search_holder {
		width: 80%;
		max-width: 900px;
		margin: 0 auto;
		margin-top: 40px;
		input {
			width: 100%;
			margin-bottom: 20px;
			padding: 5px 10px;
			border: 1px solid #3f51b5;
			border-radius: 4px;
			box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
			font-size: 16pt;
		}
	}
	.results {
		padding: 5px;
		background: #eee;
		.no_results {
			text-align: center;
			cursor: default;
			color: gray;
			padding: 17px;
			font-size: 10pt;
		}
		.result {
			background: white;
			padding: 15px;
			margin-bottom: 5px;
		}
	}
</style>